
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
body {
    background-color: black;
}

.shine[data-v-12c09182]{
  /* width: 1000px; */
  font-family: "Microsoft YaHei";
  font-size: 60px;
  text-align: center;
  background: -webkit-linear-gradient(left, rgb(198, 255, 221), rgb(251,215,134)) 0 0 no-repeat;/*设置线性渐变*/
  -webkit-background-size: 160px;                        /*设置背景大小*/
  -webkit-background-clip: text;                            /*背景被裁剪到文字*/
  -webkit-text-fill-color: rgba(61, 53, 53, 0.307);        /*设置文字的填充颜色*/
  -webkit-animation: shine-data-v-12c09182 3s infinite;                    /*设置动画*/
}
@-webkit-keyframes shine-data-v-12c09182{   /*创建动画*/
0%{
  background-position: 0 0;
}
100%{
  background-position: 100% 100%;
}
}
input[data-v-12c09182] {
  -webkit-animation: animated-border-data-v-12c09182 3s infinite;
          animation: animated-border-data-v-12c09182 3s infinite;
  width: 240px;
  font-family: Arial;
  font-size: 15px;
  line-height: 30px;
  font-weight: bold;
  color: white;
  background: rgba(61, 53, 53, 0.307);
  border: 2px solid;
  border-radius: 10px;
  padding: 15px;
}
@-webkit-keyframes animated-border-data-v-12c09182 {
0% {
    -webkit-box-shadow: 0 0 0 0 rgba(255,255,255,0.4);
            box-shadow: 0 0 0 0 rgba(255,255,255,0.4);
}
100% {
     -webkit-box-shadow: 0 0 0 20px rgba(255,255,255,0);
             box-shadow: 0 0 0 20px rgba(255,255,255,0);
}
}
@keyframes animated-border-data-v-12c09182 {
0% {
    -webkit-box-shadow: 0 0 0 0 rgba(255,255,255,0.4);
            box-shadow: 0 0 0 0 rgba(255,255,255,0.4);
}
100% {
     -webkit-box-shadow: 0 0 0 20px rgba(255,255,255,0);
             box-shadow: 0 0 0 20px rgba(255,255,255,0);
}
}
ul[data-v-12c09182] {
  list-style-type: none;
  padding: 0;
}
li[data-v-12c09182] {
  display: inline-block;
  margin: 0 10px;
}
